<template>
  <div class="chart-wrapper">
    <div id="chart"></div>
  </div>
</template>
<script>
import * as d3 from "d3";
import treeData from "./assets/tree.json";

export default {
  name: "familyTree",
  data() {
    return { treeData };
  },
  methods: {
    createSvg() {
      const margin = { top: 50, right: 200, bottom: 0, left: 0 };
      const fullWidth = 960;
      const fullHeight = 1000;
      const width = fullWidth - margin.left - margin.right;
      const height = fullHeight - margin.top - margin.bottom;

      const tree = d3
        .tree()
        .separation((a, b) => (a.children === b.children ? 100 : 100))
        .size([height, width]);

      const svg = d3
        .select("#chart")
        .append("svg")
        .attr("width", fullWidth)
        .attr("height", fullHeight);

      const g = svg
        .append("g")
        .attr("transform", `translate(${margin.left},${margin.top})`);

      g.append("text")
        .text("Children Tree")
        .attr("class", "title")
        .attr("x", 50)
        .attr("y", 50);

      // eslint-disable-next-line no-unused-vars
      const elbow = (d, i) => {
        return `M${d.source.y},${d.source.x}H${d.target.y},V${d.target.x}${
          d.target.children ? "" : "h" + margin.right
        }`;
      };

      const nodes = d3.hierarchy(treeData, (d) => d.children);

      // maps hierarchy to tree layout
      const treeNodes = tree(nodes);

      // adds links between nodes
      g.selectAll(".link")
        .data(treeNodes.links())
        .enter()
        .append("path")
        .attr("class", "link")
        .attr("d", elbow);

      const node = g
        .selectAll(".node")
        .data(treeNodes.descendants())
        .enter()
        .append("g")
        .attr("class", "node")
        .attr("transform", (d) => `translate(${d.y},${d.x})`);

      node
        .append("text")
        .attr("class", "name")
        .attr("x", 8)
        .attr("y", -6)
        .text((d) => `${d.data.name}`);
    },
  },
  mounted() {
    this.createSvg();
  },
};
</script>

<style lang="scss">
body {
  background-image: url("./assets/background.png");
  background-size: cover;
  background-repeat: no-repeat;
}
text {
  font-family: Georgia, "Times New Roman", Times, serif;
}

.title {
  font-size: 40px;
  fill: rgb(103, 32, 35);
  filter: drop-shadow(3px 3px 2px rgb(0 0 0 / 0.3));
  font-weight: bold;
  text-align: center;
}

.name {
  font-weight: bold;
}

.about {
  fill: #777;
  font-size: smaller;
}

.lifespan {
  fill: #2c5;
}

.link {
  fill: transparent;
  stroke: #000;
  shape-rendering: crispEdges;
}

.node {
  fill: rgb(103, 32, 35);
  filter: drop-shadow(3px 3px 2px rgb(0 0 0 / 0.3));
  font-size: 26px;
}
</style>
